@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
body {
  margin: 0;
  padding: 0; }

.app-container {
  height: 100vh;
  background-size: cover; }
  .app-container:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to bottom right, #002f4b, #dc4225);
    opacity: .6; }

.progress {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.navigation {
  width: 100%;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9999; }
  .navigation ul {
    min-width: 768px;
    list-style: none;
    text-align: center;
    padding: 0;
    margin: 0; }
  .navigation li {
    display: inline; }

.navigation_link {
  display: inline-block;
  text-align: center;
  text-decoration: none;
  padding: 20px 20px 5px 20px;
  font-size: 20px;
  font-family: "Open Sans";
  color: white; }
  .navigation_link:hover {
    border-bottom: 1px solid yellow;
    color: mediumblue;
    transition: color .3s ease-in-out; }

.home-container {
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

.home-container__support-container {
  max-width: 1024px;
  margin: 0 auto; }

.home-container__name {
  text-align: center;
  width: 100%;
  font-size: 80px;
  cursor: default;
  color: white;
  padding-top: 10px;
  font-family: "Open Sans"; }

.home-container__info {
  text-align: center;
  font-size: 22px;
  cursor: default;
  color: white;
  font-family: "Open Sans";
  padding-top: 10px; }

.home-container__social {
  width: 100%; }
  .home-container__social ul {
    list-style: none;
    text-align: center;
    padding: 0;
    margin: 0; }
  .home-container__social li {
    display: inline; }
  .home-container__social a {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    padding: 30px 20px 5px 20px; }
  .home-container__social img {
    height: 32px;
    width: 32px; }

.about-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center; }

.about-container__support {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  text-align: center;
  max-width: 1024px;
  margin: 0 auto;
  height: 300px; }

.about-container__avatar {
  z-index: 99999;
  border-radius: 50%;
  width: 150px;
  height: 140px;
  box-shadow: 1px 0 7px black; }

.about-container__text {
  z-index: 99999;
  font-size: 20px;
  font-family: "Open Sans";
  color: white;
  padding-top: 20px; }

.about-container__download {
  color: white;
  padding-top: 30px; }

.resume-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center; }

.resume-container__support {
  width: 100%;
  display: flex;
  justify-content: space-around;
  z-index: 99999;
  flex-wrap: wrap; }

.resume-container__cart {
  text-align: center;
  width: 225px;
  height: 275px;
  box-shadow: 20px 5px 30px black;
  font-family: "Open Sans";
  cursor: default;
  font-size: 22px;
  text-shadow: 1px 1px 2px black;
  transition: width 250ms ease-in, height 250ms ease-in, font-size 250ms ease-in; }
  .resume-container__cart:hover {
    z-index: 1;
    width: 275px;
    height: 350px;
    transition: width 250ms ease-in, height 250ms ease-in, font-size 250ms ease-in;
    font-size: 30px; }
    .resume-container__cart:hover span {
      transition: font-size 250ms ease-in;
      font-size: 18px;
      margin: 0; }
    .resume-container__cart:hover p {
      font-size: 14px;
      transition: font-size 250ms ease-in; }
  .resume-container__cart p {
    transition: font-size 250ms ease-in;
    font-size: 12px;
    margin: 0;
    padding: 0; }
  .resume-container__cart span {
    transition: font-size 250ms ease-in;
    font-size: 15px;
    padding-top: 50px;
    margin: 0; }

.resume-container__cart__main-info {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  height: 90%; }

