.app-container {
  height: 100vh;
  background-size: cover;


  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to bottom right,#002f4b,#dc4225);
    opacity: .6; } }


.progress {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }
